/*手机横屏*/
@media all and (min-width:480px) and (max-width:768px) and (orientation:landscape){
	/*************header*/
header{
		overflow:hidden;
		background:#22252A;
		display:flex;
		align-items:center;
		justify-content: space-between;
		h1{
			.header_a{
				display:block;
				text-indent:-5000px;
				background:url(/static/images/logo.png) no-repeat left top;
				background-size:100% 100%;
				width:0.5rem;
				height:0.5rem;
				font-size:$r16;
				
			}
			margin-left:0.15rem;
			margin-right:0.15rem;
		}
		a{
			font-size:.14rem;
			display:block;
			color:#fff;
		}
		ul{
			padding-right:.1rem;
			display: flex;
			justify-content: flex-end;
			li{
				font-size:.12rem;
				color:#fff;
				display: flex;
				justify-content: flex-end;
				margin-left: .1rem;
				a{
					font-size: .12rem;
				}
			}
		}
	
}
/**************navpc*/
#navpc{
	display:none;
}
/**************************banner*/
$width:3.75rem;
$w:7.50/2;
$h:3.75/2;
#banner{
	.swiper_c_modi{
		margin: 0;
		position:relative;
		overflow:hidden;
		width:100%;
		height:#{$h}rem;
		padding:0 0.2rem;
		.parallax{
			height:#{$h}rem;
			width:4.8rem;
			background-size:100% 100%;
			position:absolute;
			left:-0.2rem;
		}
		.swiper-wrapper{
			.swiper-slide{
				background-size:100% 100%;
				height:1.8rem;
				img{
					width:100%;
					height: 100%;
				}
			}

		}
		
		
		

	}
	
}
/*******************************nav*/
nav{
	width:3.75rem;
	height:1rem;
	background:#22252A;
	display:flex;
	align-items:center;
	
	div{
		font-size:0.4rem !important;
		color:#19b5fe;
		flex:1;
		display:flex;
		justify-content:center;
	}
}
/****************************section*/
section{
	#iscrollLoading{
		width:3.75rem;
		.is_title{
			font-size:0.26rem;
			color:#6eb764;
			font-weight:bold;
			text-align:center;
			padding:0.08rem 0;
			background:linear-gradient(to left,rgba(207,207,207,1)  10%,rgba(0,0,0,1) ,rgba(207,207,207,1) 100%)
		}
		#iscrollWrapper {
			width: 100%;
			height:4.2rem;
			background: #ccc;
			overflow: hidden;
			font-size: 24px;
			line-height: 30px;
			font-weight: bold;
			text-align: center;
			margin-bottom: 0.6rem;
			ul{
				overflow:hidden;
				li:nth-child(2n+1){
					background: #ccc;
					color: green;
				}
				li:nth-child(2n){
					background: #ccc;
					color:#19b5fe;
				}
				li{
					overflow:hidden;
					padding:0.20rem 0.15rem;
					img{
						width:100%;
					}
					
					h3{
						font-size:0.24rem;
						color:#f9844b;
						margin:0.1rem 0;
						text-align:left;

					}
					p{
						font-size:0.18rem;
					}
				}
			}

		}
		

			 
			
	}
}

/******************************footer*/
footer{
	width:3.75rem;
	height:0.3rem;
	display:flex;
	position:fixed;
	z-index:1000;
	left:0;
	bottom:0;
	background:#f1f1f1;
	border-top:#b8b8b8 solid 0.5px;
	a{
		flex:auto;
		height: 100%;
		div{
			flex:auto;
			display:flex;
			flex-direction:column;
			justify-content:center;
			align-items:center;
			
			span{
				font-size:0.06rem !important;
			}
			
		}
	}
	#index{
		font-size:0.15rem !important;
		//color:#6DDB4F;
	}
	#demo{
		font-size:0.14rem !important;

	}
	#blog{
		font-size:0.14rem !important;

	}
	#scrawl{
		font-size:0.155rem !important;

	}
}

/********************bottom*/
#bottom{
	display:none !important;
}

	
}/*@media end*/


