/*电脑*/
@media all and (min-width:1025px){

/*************header*/
header{
		width:3.75rem;
		overflow:hidden;
		background:#22252A;
		display:flex;
		align-items:center;
		justify-content: space-between;
		height:0.15rem;
		h1{
			.header_a{
				display:block;
				text-indent:-5000px;
				background:url(/static/images/logo.png) no-repeat left top;
				background-size:100% 100%;
				width:0.1rem;
				height:0.1rem;
				font-size:$r16;
				
			}
			margin-left:0.15rem;
			margin-right:0.15rem;
		}
		a{
			font-size:0.08rem;
			display:block;
			color:#fff;
		}
		ul{
			padding-right:.1rem;
			display: flex;
			justify-content: flex-end;
			li{
				font-size:.05rem;
				color:#fff;
				display: flex;
				justify-content: flex-end;
				margin-left: .1rem;
				a{
					font-size: .05rem;
				}
			}
		}
	
}
/********************navpc*/
#navpc{
	display:block;
	height:0.21rem;
	.navpcpre0{
		display:block;
		width:3.75rem;
		height:0.01rem;
		background:linear-gradient(to right,rgba(0,0,0,1) 5%,rgba(255,0,0,1) 13%,rgba(0,0,0,1) 22%);
	}
	.navpcpre1{
		display:block;
		width:3.75rem;
		height:0.01rem;
		background:linear-gradient(to right,rgba(0,0,0,1) 28%,rgba(255,0,0,1) 37%,rgba(0,0,0,1) 47%);
	}
	.navpcpre2{
		display:block;
		width:3.75rem;
		height:0.01rem;
		background:linear-gradient(to right,rgba(0,0,0,1) 52%,rgba(255,0,0,1) 63%,rgba(0,0,0,1) 72%);
	}
	.navpcpre3{
		display:block;
		width:3.75rem;
		height:0.01rem;
		background:linear-gradient(to right,rgba(0,0,0,1) 78%,rgba(255,0,0,1) 88%,rgba(0,0,0,1) 96%);
	}
	
	ul{
		width:100%;
		
		display:flex;
		li{

			flex:1;
			height:0.2rem;
			display:flex;
			align-items:center;
			justify-content:center;

			a{
				font-size:0.1rem;
				color:#ccc;
			}
			&.navpcfirsta a{
				color:red;
			}
			
		}
	}
	
	
}

/**************************banner*/
#banner{
	.swiper_c_modi{
		margin: 0;
		position:relative;
		overflow:hidden;
		width:100%;
		height:1rem;
		padding:0 0.2rem;
		.parallax{
			height:1rem;
			width:5rem;
			background:no-repeat;
			background-size:100% 100%;
			position:absolute;
			left:-0.2rem;
		}
		.swiper-wrapper{
			height: 100%;
			margin-top:-0.3rem;
			.swiper-slide{
				background-size:100% 100%;
				height:1rem;
				img{
					width:100%;
					height: 100%;
				}
			}

		}
		
		
		

	}
	
}
/*******************************nav*/
nav{
	width:3.75rem;
	margin:0 auto;
	height:0.4rem;
	background:#22252A;
	display:flex;
	align-items:center;

	div{
		font-size:0.2rem !important;
		color:#19b5fe;
		flex:1;
		display:flex;
		justify-content:center;
	}
}
/****************************section*/
section{
	#iscrollLoading{
		width:3.75rem;
		clear:both;
		.is_title{
			font-size:0.08rem;
			color:#6eb764;
			font-weight:bold;
			text-align:center;
			padding:0.01rem 0;
			background:linear-gradient(to left,rgba(207,207,207,1)  10%,rgba(0,0,0,1) ,rgba(207,207,207,1) 100%)
		}
		#iscrollWrapper {
			width: 100%;
			height:3rem;
			background: #ccc;
			overflow: hidden;
			font-size: 24px;
			line-height: 30px;
			font-weight: bold;
			text-align: center;
			ul{
				overflow:hidden;
				li:nth-child(2n+1){
					background: #ccc;
					color: green;
				}
				li:nth-child(2n){
					background: #ccc;
					color:#19b5fe;
				}
				li{
					width:1.2rem;
					float:left;
					overflow:hidden;
					padding:0.20rem 0.15rem;
					img{
						width:100%;
					}
					
					h3{
						font-size:0.1rem;
						color:#f9844b;
						margin:0.1rem 0;
						text-align:left;

					}
					p{
						font-size:0.06rem;
					}
				}
			}

		}
		

			 
			
	}
}

/******************************footer*/
footer{
	display:none;
	
}

}/*@media end*/




