@charset "utf-8";

html{font-size:26.66666667vw;-ms-touch-action: none;}
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, fieldset, input, p, blockquote, th, td { margin:0; padding:0; }
body { font:26.6666666667vw "微软雅黑"; color:#333; background:#fff;}
fieldset, img { border:0; }
ol, ul { list-style:none; }
h1, h2, h3, h4, h5, h6{ font-size:100%; }
em { font-style:normal; }
input, button, select, textarea { outline:none; } 
textarea { resize:none; }
p{ text-align:justify;}
a { color: #666; text-decoration:none; }
a:hover { color: #6DDB4F; text-decoration:none; }
.clearfix:after{content:"";/*里面可以为空、内容的意思*/
                display:block;
                clear:both;
                visibility:hidden; /*隐藏*/
                height:0;
                overflow:hidden;
}
.clearfix{zoom:1;/*这个转为解决IE6的bug的*/}

